import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  DatePicker,
  message
} from 'antd'
import {CsxSelect, CsxFormSearch, CsxSearch} from 'Form'
import {API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES, API_WDENTRYORDERS_PRODUCTS, API_WDENTRYORDERS_SUPPLIERS} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxValidateInput} from 'Form'
import moment from 'moment'
const confirm = Modal.confirm

@connect(
  state => ({...state.withoutOrderInWarehouse, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '操作',
      width: 100,
      key: 'caozuo',
      dataIndex: 'caozuo',
      render: (text, record, index) => <div className="tableOperation">
        {
          record.productCode ? <span className='red' onClick={() => {
            for (let key in record) {
              if (key == 'id') continue
              delete record[key]
            }
            this.forceUpdate()
          }}>删除</span> : null
        }
      </div>
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode',
      render: (text, record, index) => {
        const {userLoation, supplierCode} = this.props
        return <CsxFormSearch
          dateApi={API_WDENTRYORDERS_PRODUCTS}
          searchParams={{warehouseCode: userLoation.value, supplierCode: supplierCode && supplierCode.value}}
          searchParamsKey='productCode'
          selectItem={{data: record.productCode, key: 'productCode', valueKey: ['productCode', 'productName']}}
          onChangeSelect={(value) => {
            record.productCode = value
            this.forceUpdate()
          }}
          onSelect={(value, valueObj) => {
            const {handleGetGoodsPrice, userLoation} = this.props
            record.productCode = value
            record.productName = valueObj.productName
            record.productBarCode = valueObj.barCode
            record.unit = valueObj.unit
            record.qty = {value: '', error: true}
            record.amount = {value: '', error: true}
            record.taxRate = valueObj.productInputTaxRate
            record.taxCode = valueObj.productInputTaxCode
            record.produceDateMoment = moment()
            record.produceDate = moment().format('YYYY-MM-DD')
            handleGetGoodsPrice({searchParams: {productCode: value.value, locationCode: userLoation.value, supplierCode: supplierCode && supplierCode.value}}, (data) => {
              record.price = {value: data.newPrice || '', error: data.newPrice ? false : true, disabled: data.newPrice ? false : true}
              this.forceUpdate()
            })
          }}
        />
      }
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '收货单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '生产日期',
      width: 150,
      dataIndex: 'produceDate',
      key: 'produceDate',
      render: (text, record, index) => <DatePicker value={record.produceDateMoment} onChange={(moment, string) => {
        record.produceDate = string
        record.produceDateMoment = moment
        this.forceUpdate()
      }} format={'YYYY-MM-DD'} disabledDate={(current) => current && current.valueOf() > Date.now()}/>
    }, {
      title: '进价（元）',
      width: 150,
      dataIndex: 'price',
      key: 'price',
      render: (text, record, index) => <CsxValidateInput style={{width: '100px'}} reg= {'TWOO'} value={record.price ? record.price.value == undefined ? record.price : record.price.value : ''} error={record.price && record.price.error || false} handleOnchange={ (data) => {
        record.price = data
        if (record.qty && record.qty.value) {
          record.amount = (Number(record.price.value) * Number(record.qty.value)).toFixed(4)
        }
        this.forceUpdate()
      }} disabled={record.price && record.price.disabled}/>
    }, {
      title: '已收量',
      width: 150,
      key: 'ysl',
      render: (text, record, index) => <CsxValidateInput style={{width: '100px'}} reg= {(record.unit && record.unit.toUpperCase()) == 'KG' ? 'TWOO' : 'REGO'} value={record.qty ? record.qty.value == undefined ? record.qty : record.qty.value : ''} error={record.qty && record.qty.error || false} handleOnchange={ (data) => {
        record.qty = data
        record.amount = (Number(record.price.value) * Number(record.qty.value)).toFixed(4)
        this.forceUpdate()
      }} />
    }, {
      title: '总价（元）',
      width: 150,
      dataIndex: 'amount',
      key: 'amount'
    }]
    // }, {
    //   title: '备注',
    //   width: 100,
    //   dataIndex: 'remark',
    //   key: 'remark',
    //   render: (text, record, index) => <Input style={{width: '100px'}} onChange={(e) => {
    //     record.remark = e.target.value
    //   }}/>
    // }]
  }
  componentDidMount() {
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowCreateModal: false})
  }
  // 确认
  handleCommit = () => {
    const {handleCommit, createTable, shipperCode, userLoation, supplierCode} = this.props
    if (shipperCode && shipperCode.value) {
      if (!supplierCode || !supplierCode.value) {
        message.error('请选择供应商')
        return
      }
      let req = {}
      req.items = JSON.parse(JSON.stringify(createTable)).filter((o , i) => o.productCode)
      if (req.items.length == 0) {
        message.error('请输入商品编码')
        return
      }
      let error = false //收货数量的格式判断
      let noProduceDate = false //判断收货是否有填写生产日期
      req.items.map((o) => {
        if (o.price.error || o.qty.error || o.amount.error) {
          error = true
          return
        }
        if (!o.produceDate) {
          noProduceDate = true
          return
        }
        o.productCode = o.productCode && o.productCode.value

        o.price = o.price && o.price.value
        o.qty = o.qty && o.qty.value
        // o.amount = o.amount && o.amount.value
        delete o.produceDateMoment
      })
      if (error) {
        message.error('请填写正确的数值')
        return
      }
      if (noProduceDate) {
        message.error('请选择生产日期')
        return
      }
      req.header = {
        shipperCode: shipperCode.value,
        shipperName: shipperCode.children,
        supplierCode: supplierCode.value,
        supplierName: supplierCode.children,
        receiveLocationCode: userLoation.value,
        receiveLocationName: userLoation.children
      }
      let _this = this
      confirm({
        title: '提示',
        content: '所有商品都已收货，系统将自动关单，关单后无法再收货',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleCommit({data: req}, () => {
            message.success('创建成功')
            const {handleGetList, searchParams, page, size} = _this.props
            handleGetList({searchParams: {page, size, ...searchParams}})
            _this.handleCloseModal()
          })
        },
        onCancel() {
          _this.handleCloseModal()
        }
      })
    } else {
      message.error('请选择货主')
    }
  }
  render() {
    const {createTableLoading, createTable, shipperCode, userLoation, update, supplierCode, lastSupplierCode} = this.props
    return (
        <Modal
          title="创建无单入库"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          style={{height: '100%'}}
          className='full'
        >
            <div className='content'>
              <Row type="flex" justify="start">
                <Col span={8}>
                  <div className='commonInput'>
                    <label>入库地点：</label>
                    <div className='box'>
                      <span>{userLoation && userLoation.value + userLoation.children || ''}</span>
                    </div>
                  </div>
                </Col>
                <Col span={8}>
                  <CsxSelect
                    dateApi={API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES}
                    searchParams={{operationType: 'S'}}
                    label='货主'
                    selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
                    onChangeSelect={(value) => {
                      update({shipperCode: value})
                    }}
                  />
                </Col>
                <Col span={8}>
                  <CsxSearch
                    dateApi={API_WDENTRYORDERS_SUPPLIERS}
                    label="供应商"
                    searchParams={{warehouseCode: userLoation.value}}
                    searchParamsKey='supplierCode'
                    selectItem={{data: supplierCode, key: 'key', valueKey: ['key', 'value']}}
                    onChangeSelect={(value) => {
                      update({supplierCode: value})
                    }}
                    onSelect={(value) => {
                      let supplierCodeObj = {
                        supplierCode: value,
                        lastSupplierCode: value
                      }
                      if (!lastSupplierCode || value.value != lastSupplierCode.value) {
                        let createTable = new Array(100).fill({})
                        createTable.map((o, i) => {
                          createTable[i] = {id: i}
                        })
                        supplierCodeObj.createTable = createTable
                      }
                      update(supplierCodeObj)
                    }}
                  />
                </Col>
              </Row>
              <div className='table-group' style={{margin: 0}}>
                <Table
                  rowKey={(record, index) => index}
                  loading={createTableLoading}
                  columns={this.columns}
                  scroll={{x: 1750, y: 400}}
                  dataSource={createTable}
                  pagination={false}
                />
                <Row type="flex" justify="start" style={{marginTop: '15px'}}>
                  <Col>
                    <p style={{color: 'red'}}>注: 称重单位（kg）可输入大于0的三位小数，非称重单位只能输入大于0的整数</p>
                  </Col>
                </Row>
              </div>
              <Row type="flex" justify="end" style={{marginTop: '15px'}}>
                <Col>
                  <Button type="primary" onClick={this.handleCommit} >提交</Button>
                </Col>
              </Row>
            </div>
        </Modal>
    )
  }
}



