import * as actionType from './actionType'
import {getData} from 'util'
import {API_ENTRYORDERS_PAGE, API_ENTRYORDERS_ENTRYORDERTRACE_PAGE, API_WDENTRYORDERS_CREATE, API_WDENTRYORDERS_PRODUCTS, API_ENTRYORDERS_EXPORT, API_WDENTRYORDERS_PRODUCTPRICE} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams, type: 'W'}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams
  getData({api: API_ENTRYORDERS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}

// 获取订单详情
export const handleGetDetail = (reqData) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {tableLoading: true}})
  getData({api: API_ENTRYORDERS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        tableLoading: false,
        modalData: data.result || {}
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}

// 获取入库单操作记录
export const handleGetDetailRecord = (reqData) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {tableLoading: true}})
  getData({api: API_ENTRYORDERS_ENTRYORDERTRACE_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        tableLoading: false,
        searchResultRecord: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}

// 创建无单入库
export const handleCommit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {tableLoading: true}})
  let api = API_WDENTRYORDERS_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        tableLoading: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}

// 获取商品信息
export const handleGetGoodsInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {createTableLoading: true}})
  let api = API_WDENTRYORDERS_PRODUCTS
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack&&callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObjSearchParams = {...reqData.searchParams, type: 'W'}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams
  getData({api: API_ENTRYORDERS_EXPORT, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 获取商品进价
export const handleGetGoodsPrice = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE, data: {createTableLoading: true}})
  let api = API_WDENTRYORDERS_PRODUCTPRICE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack&&callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.WITHOUTORDERINWAREHOUSE_FILE
    })
  })
}


